import Home from './components/Home/Home';
import History from './components/History/History';
import Invite from './components/Invite/Invite.jsx';
import Album from './components/Album/Album.jsx';

import Promises from './components/Promises/Promises.jsx';
import Hope from './components/Hope/Hope.jsx';
import Footer from './components/Footer/Footer.jsx';

import { motion } from 'framer-motion';
import {Map, Placemark, YMaps} from "@pbe/react-yandex-maps";

const praseAnimation = {
  hidden: {
    y: 100,
    opacity: 0
  },
  visible: {
    y: 0,
    opacity: 1,
    transition: {duration: 2, type: "spring"}
  }
}

const parallax = document.getElementById("parallax");

window.addEventListener("skroll", () => {
  let offset = window.pageYOffset;
  // offset-=3100;
  parallax.style.backgroundPositionY = offset*(0.1) + "px";
})



function App() {
  return (
    <div className="App">
      <Home/>
      <History/>
      <Invite/>
      <Album/>
      <motion.section 
        initial="hidden"
        whileInView="visible"
        viewport={{amount: 1}}
        className='citata'
      >
        <motion.h1 variants={praseAnimation}>Для всего мира ты один человек, но <br /> для одного человека ты - весь мир</motion.h1>
      </motion.section>
      <div className='hands' id="parallax"></div>
      <Promises/>
      <Hope/>
      <YMaps>
        <Map defaultState={{ center: [51.6628706271168, 39.20282639701857], zoom: 20 }} width={"100vw"} height={"700px"}>
          <Placemark defaultGeometry={[51.6628706271168, 39.20282639701857]} />
        </Map>
      </YMaps>
      <Footer/>
    </div>
  );
}

export default App;
