import React from 'react';
import historyIMG from '../../images/history.jpg'
import counter1 from '../../images/counter_1.svg'
import counter2 from '../../images/counter_2.svg'
import cl from "./History.module.css"
import {delay, easeOut, motion} from "framer-motion"


const titleAnimation = {
  hidden: {
    y: -100,
    opacity: 0,
    
  },
  visible: {
    y: 0,
    opacity: 1,
    transition: {duration: 0.5}
  },
}

const imgAnimation = {
  hidden: {
    x: -100,
    opacity: 0
  },
  visible: {
    x: 0,
    opacity: 1,
    transition: {duration: 0.6, type: "spring", ease: "linear"}
  },
}

const listAnimation = {
  hidden: {
    x: -100,
    opacity: 0
  },
  visible: custom => ({
    x: 0,
    opacity: 1,
    transition: {delay: custom * 0.4,  duration: 0.5, type: "tween", easeOut: "linear"}
  }),
}

const History = () => {
  return(
    <motion.section 
      initial="hidden"
      whileInView="visible"
      viewport={{amount: 0.5}}
      className={cl.main_history}
    >
      <div className='container'>
          <motion.h2 variants={titleAnimation} transition={{delay: 0.5}} className={cl.phrase}>
            За каждой большой любовью <br /> стоит большая история
          </motion.h2>
          <div className={cl.history}>
            <motion.img variants={imgAnimation} transition={{delay: 0.8}} src={historyIMG} alt="" style={{borderRadius: 20}}/>
            <ul className={cl.mainList}>
              <motion.li custom={1} variants={listAnimation} className={cl.list}>
                <img src={counter1} alt=""/>
                <div className={cl.listInfo}>
                  <h3 className={cl.listName}>
                    Церемония
                  </h3>
                  <p className={cl.text}>
                    12:00 городской округ Воронеж, Ленинский район, <br /> площадь Ленина, 11
                  </p> 
                </div>
              </motion.li>
              <motion.li custom={2} variants={listAnimation} className={cl.list}>
                <img src={counter2} alt=""/>
                <div className={cl.listInfo}>
                  <h3 className={cl.listName}>
                    Приём
                  </h3>
                  <p className={cl.text}>
                    16:00 городской округ Воронеж, Ленинский район, <br /> улица Краснознамённая, 61
                  </p>
                </div>
              </motion.li>
            </ul>
          </div>
        </div>
      </motion.section>
  );
};

export default History;