import React from 'react';
import {MPhoto} from './Photo';
import { delay, easeOut, motion } from 'framer-motion';
import img1 from '../../images/album_photo1.jpg'
import img2 from '../../images/album_photo2.jpg'
import img3 from '../../images/album_photo3.jpg'
import cl from "./Album.module.css"


const imgAnimation = {
  hidden: {
    x: -100,
    opacity: 0
  },
  visible: custom => ({
    x: 0,
    opacity: 1,
    transition: {duration: 0.7, type: "spring", easeOut: "linear", delay: custom * 0.2}
  })
}

const Album = () => {
  return(
    <motion.section 
      initial="hidden"
      whileInView="visible"
      viewport={{amount: 0.4}}
      className={cl.album}
    >
        <div className={cl.photos}>
          <MPhoto variants={imgAnimation} custom={1} img={img1}/>
          <MPhoto variants={imgAnimation} custom={2} img={img2}/>
          <MPhoto variants={imgAnimation} custom={3} img={img3}/>
        </div>
    </motion.section>
  );
};

export default Album;