import React from 'react';
import promiseIMG from "../../images/promiseImg.jpg"
import cl from "./Promises.module.css"
import { delay, motion } from 'framer-motion';

const textAnimation = {
  hidden: {
    y: 100,
    opacity: 0
  },
  visible: custom => ({
    y: 0,
    opacity: 1,
    transition: {delay: custom * 0.2, duration: 0.7, type: "spring"}
  })
}

const btnAnimation = {
  hidden: {
    y: 100,
    opacity: 0
  },
  visible: {
    y: 0,
    opacity: 1,
    transition: {duration: 0.7, type: "spring", delay: 0.6}
  }
}

const imgAnimation = {
  hidden: {
    x: -100,
    opacity: 0
  },
  visible: {
    x: 0,
    opacity: 1,
    transition: {duration: 0.7, type: "spring"}
  }
}

const Promises = () => {
  return(
    <motion.section 
      initial="hidden"
      whileInView="visible"
      viewport={{amount: 0.7}}
      className={cl.main_promises}
    >
      <div className='container'>
        <div className={cl.promises}>
          <div className={cl.promises_text}>
            <motion.h3 variants={textAnimation} custom={1}>Обещания</motion.h3>
            <motion.p variants={textAnimation} custom={2}>Нам очень хотелось бы, чтобы Вы смогли стать свидетелем этого важного для нас события и разделить с нами радостные моменты в волнующей атмосфере любви и счастья! Нам будет очень приятно, если Вы разделите с нами лучшие минуты этого счастливого дня и сохраните о нем в сердце самые теплые воспоминания.</motion.p>
          </div>
          <div>
            <motion.img variants={imgAnimation} transition={{delay: 0.2}} src={promiseIMG} alt="" style={{borderRadius: 20}}/> 
          </div>
        </div>
      </div>
    </motion.section>
  );
};

export default Promises;