import React from 'react';
import Marquee from 'react-double-marquee'
import cl from "./RunningLine.module.css"

const RunningLine = () => {
  return(
    <div className={cl.run}>
      <Marquee direction={"left"}>
        © Данн К.Н., Чернышов С.Б., 2024. Сайт сделан в исключительно ознакомительных целях. Любое совпадение с иными сайтами случайно.
      </Marquee>
    </div>
  );
};

export default RunningLine;