import React, {useState} from 'react';
import {MMyButton} from "../Button/MyButton"
import cl from "./Hope.module.css"
import { delay, motion } from 'framer-motion';
import ModalInvite from "../Modal/ModalInvite";
import checkInvite from "../../images/check.svg"

const hopeAnimation = {
  hidden: {
    y: 100,
    opacity: 0,
  },
  visible: custom => ({
    y: 0,
    opacity: 1,
    transition: {delay: custom * 0.3, duration: 0.6, type: "tween"}
  })
}

const Hope = () => {
  const [modalActive, setModalActive] = useState(false)
  
  return(
    <motion.section 
      initial="hidden"
      whileInView="visible"
      viewport={{amount: 0.2}}
      className={cl.hope}
    >
      <div className='container'>
        <motion.h1 variants={hopeAnimation} custom={1}>
          Надеемся увидеть Вас!
        </motion.h1>
        <div className={cl.btn__wrapper}>
          <motion.button className={cl.btn} variants={hopeAnimation} custom={2} onClick={() => setModalActive(true)}>
            Просьба ответить
          </motion.button>
        </div>
        <ModalInvite active={modalActive}>
          <h1 style={{color: "#000000"}}>Спасибо, что вы приняли наше приглашение</h1>
          <div style={{display: "flex", justifyContent: "center", alignItems: "center", marginTop: 50}} onClick={() => setModalActive(false)}>
            <img className={cl.check} src={checkInvite} alt=""/>
          </div>
        </ModalInvite>
      </div>
    </motion.section>
  );
}

export default Hope;