import React, {useEffect, useRef, useState} from 'react';
import './timer.css'
import {motion} from "framer-motion"




const Timer = ({ref}) => {

  const [timerDays, setTimerDays] = useState("00");
  const [timerHours, setTimerHours] = useState("00");
  const [timerMinutes, setTimerMinutes] = useState("00");
  const [timerSeconds, setTimerSeconds] = useState("00");

  let interval = useRef;

  const startTimer = () => {
    const CountdownDate = new Date("September 28 2024 00:12:00").getTime();

    interval = setInterval(() => {
      const now = new Date().getTime();
      const distance = CountdownDate - now;

      const days = Math.floor(distance / (1000 * 60 * 60 * 24));
      const hours = Math.floor((distance % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60));
      const minutes = Math.floor((distance % (1000 * 60 * 60)) / (1000 * 60));
      const seconds = Math.floor((distance % (1000 * 60)) / 1000);

      if(distance < 0) {
        // stop our timer
        clearInterval(interval.current);
      } else {
        setTimerDays(days);
        setTimerHours(hours);
        setTimerMinutes(minutes);
        setTimerSeconds(seconds);
      }

    }, 1000);
  }

  useEffect(() => {
    startTimer();
    return () => {
      clearInterval(interval.current);
    };
  }, []);

  

  return(
    <section className="timer" ref={ref}>
      <div>
        <h2>До начала церемонии</h2>
        <p>Осталось всего лишь</p>
      </div>
      <div>
        <section>
          <p>{timerDays}</p>
          <p><small>Дней</small></p>
        </section>
        <section>
          <p>{timerHours}</p>
          <p><small>Часов</small></p>
        </section>
        <section>
          <p>{timerMinutes}</p>
          <p><small>Минут</small></p>
        </section>
        <section>
          <p>{timerSeconds}</p>
          <p><small>Секунд</small></p>
        </section>
      </div>
    </section>
  );
};

export default Timer;