import React from 'react';
import "./modalInvite.css"

const ModalInvite = ({active, children}) => {
  return (
      <div className={active ? "modal-invite active" : "modal-invite"} >
        <div className="modal-invite__content">
          {children}
        </div>
      </div>
  );
};

export default ModalInvite;