import React from 'react';
import Timer from '../Timer/Timer';
import cl from "./Invite.module.css"
import {delay, motion} from "framer-motion"


const timerAnimation = {
    hidden: {
      x: -100,
      opacity: 0
    },
    visible: {
      x: 0,
      opacity: 1
    },
  }

  const textAnimation = {
    hidden: {
      // x: -100,
      opacity: 0
    },
    visible: custom => ({
      // x: 0,
      opacity: 1,
      transition: {
        delay: custom * 0.4,
        duration: 2,
      }
    }),
  }

const Invite = () => {
  return(
    <motion.section 
      initial="hidden"
      whileInView="visible"
      className={cl.invite}
    >
      <Timer variants={timerAnimation}/>
      <motion.div 
        initial="hidden"
        whileInView="visible"
        viewport={{amount: 0.5}}
        className={cl.letter}
      >
        <motion.h2 variants={textAnimation} custom={1}>Дорогой гость, </motion.h2>
        <motion.h3 variants={textAnimation} custom={2}>Приглашаем вас на День рождения нашей семьи!</motion.h3>
        <motion.p variants={textAnimation} custom={3} style={{lineHeight: "60px"}}>В прекрасный день семьи рожденья, <br /> В день двух сердец соединенья, <br /> В торжественный и светлый час <br /> Мы будем рады видеть Вас!</motion.p>
      </motion.div>
    </motion.section>
  );
};

export default Invite