import React, { useState } from 'react';
import vk from "../../images/vk.svg"
import vk_black from "../../images/vk_black.svg"
import cl from "./Footer.module.css"
import RunningLine from '../RunningLine/RunningLine';
import Modal from '../Modal/Modal';

const Footer = () => {
  const [modalActive, setModalActive] = useState(false)

  return(
    <section className={cl.footer}>
      <div className='container'>
        <div className={cl.socials}>
          <div className={cl.vk_social}>
            <a href="https://vk.com/id564714753" target="_blank">
              <img src={vk} alt="" />
            </a>
            <a href="https://vk.com/id564714753" target="_blank" style={{fontSize: 20}} >
              Степан
            </a>
          </div>
          <div className={cl.hearts}></div>
          <div className={cl.vk_social}>
            <a href="https://vk.com/bebyyya" target="_blank">
              <img src={vk} alt="" />
            </a>
            <a href="https://vk.com/bebyyya" target="_blank" style={{fontSize: 20}}>
              Яна
            </a>
          </div>
        </div>
        <div className={cl.btn__wrapper}>
          <button type="submit" className={cl.btn} onClick={() => setModalActive(true)}>Поддержать авторов</button>
        </div>
        <Modal active={modalActive} setActive={setModalActive}>
          <h3 style={{
            textAlign: "center",
            marginBottom: 20,
            color: "#000000"
          }}>
          Поддержать авторов сайта</h3>
          <div className={cl.authors}>
            <div className={cl.vk_social}>
              <a href="https://vk.com/id536371394" target="_blank"><img src={vk_black} alt=""/></a>
              <a href="https://vk.com/id536371394" target="_blank">Кирилл</a>
            </div>
            <div className={cl.vk_social}>
              <a href="https://vk.com/schernyshov1998" target="_blank"><img src={vk_black} alt=""/></a>
              <a href="https://vk.com/id536371394" target="_blank">Сергей</a>
            </div>
          </div>
        </Modal>
        <RunningLine/>
      </div>
    </section>
  );
};

export default Footer;